.searchBar{position:fixed;top:-160px;left:0;z-index:1;background-color:#fff;padding-top:38px;padding-bottom:38px;box-shadow:0 1px 18px rgba(0,0,0,.102);transition:all .5s ease;opacity:0}.searchBar.search-active{top:0;opacity:1;transition:all .3s ease}.searchBar input{height:60px;border:0;font-size:18px}.searchBar .input-group{display:flex;align-items:center}.searchBar .input-group-text{background-color:#fff;border:0;cursor:pointer;user-select:none}.searchBar .input-group-text img{width:18px}.searchBar .input-group-text.nav-search{width:40px;height:40px;background:#e3e3e3;border-radius:144px;font-size:27px;margin-left:24px}